import type {
  MetaFunction,
  LinksFunction,
  HtmlMetaDescriptor,
  LinkDescriptor
} from '@remix-run/node'
import { Links, LiveReload, Meta, Outlet, ScrollRestoration } from '@remix-run/react'
import { meta as rootMeta, links as rootLinks } from '~/utils/root'
import { globalStyles } from '~/styles'

export const meta: MetaFunction = () => rootMeta as HtmlMetaDescriptor
export const links: LinksFunction = () => rootLinks as LinkDescriptor[]

export default function App() {
  globalStyles()
  return (
    <html lang="en">
      <head>
        <Meta />
        <Links />
      </head>
      <body>
        <Outlet />
        <ScrollRestoration />
        {process.env.NODE_ENV === 'development' && <LiveReload />}
      </body>
    </html>
  )
}
